* {
    box-sizing: border-box;
    font-family: -apple-system, system-ui, 'Segoe UI', Helvetica, Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji';
}

.abra-prodej-app {
    width: 100%;
    height: 99vh;
    tfoot {
        font-weight: bold;
    }
    button {
        border-radius: 3px;
        border: 1px solid black;
        &:hover:not([disabled]) {
            cursor: pointer;
            background-color: rgb(188, 231, 248);
            border: 2px solid rgb(100, 150, 232);
        }
        &:hover[disabled] {
            cursor: not-allowed;
        }
    }
    .item-alignment-right {
        text-align: right;
    }
    .header {
        display: flex;
        flex-direction: row;
        align-items: center;
        border: 1px solid;
        margin: 2px;
        height: 40px;
        @media only screen and (max-width: 700px) {
            width: 100%;
            height: 5%;
            justify-content: center;
        }
        &__details {
            padding-left: 10px;
            display: flex;
            align-items: center;
            span {
                margin-right: 10px;
            }
        }
    }
    &__container {
        height: calc(100vh - 27px);
        display: flex;
        flex-direction: row;
        align-items: center;
        // justify-content: center;
        @media only screen and (max-width: 700px) {
            flex-direction: column;
        }
        &__firms {
            width: 80%;
            height: 100%;
            border: 1px solid;
            margin: 2px;
            padding: 2px;
            @media only screen and (max-width: 700px) {
                width: 100%;
                height: 65%;
            }
            table {
                width: 100%;
                position: relative;
                text-align: left;
                td {
                    padding-left: 10px;
                    padding-right: 10px;
                }
                thead th {
                    position: sticky;
                    top: 0;
                    background-color: rgb(79, 79, 79);
                    color: white;
                    opacity: 1;
                    padding-left: 10px;
                    padding-right: 10px;
                }
            }
            &__name {
                font-weight: bold;
                padding: 0px 0px 0px 5px;
                display: flex;
                text-align: left;
                align-items: center;
                border-bottom: 1px solid black;
                height: 25px;
            }
            &__list-container {
                width: 100%;
                height: calc(100% - 27px);
                margin: 2px;
                overflow-y: scroll;
                &__item {
                    svg {
                        width: 15px;
                        height: 15px;
                        padding: 2px;
                        opacity: 0.5;
                        transition: all 0.3s ease-in-out;
                    }
                    &:hover {
                        background-color: rgb(205, 233, 242);
                        cursor: pointer;
                        svg {
                            opacity: 0.9;
                            transform: scale(2);
                        }
                    }
                }
            }
        }
        &__functions {
            height: 100%;
            width: 20%;
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            border: 1px solid;
            font-weight: bold;
            margin: 2px;
            @media only screen and (max-width: 700px) {
                flex-direction: row;
                width: 100%;
                height: 30%;
            }
            button {
                width: 95%;
                appearance: none;
                background-color: #2ea44f;
                border: 1px solid rgba(27, 31, 35, 0.15);
                border-radius: 6px;
                box-shadow: rgba(27, 31, 35, 0.1) 0 1px 0;
                box-sizing: border-box;
                color: #fff;
                cursor: pointer;
                display: inline-block;
                font-family: -apple-system, system-ui, 'Segoe UI', Helvetica, Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji';
                font-size: 14px;
                font-weight: 600;
                line-height: 20px;
                padding: 6px 16px;
                position: relative;
                text-align: center;
                text-decoration: none;
                user-select: none;
                -webkit-user-select: none;
                touch-action: manipulation;
                vertical-align: middle;
                white-space: nowrap;
                &:hover {
                    // background-color: #2c974b;
                    background-color: #415ed4;
                    color: red;
                }
            }
            &__kohorts {
                width: 100%;
                display: flex;
                justify-content: center;
                align-items: center;
                flex-direction: column;
                @media only screen and (max-width: 700px) {
                    width: 50%;
                }
                button {
                    width: 95%;
                }
                p {
                    width: 95%;
                    border: 1px solid;
                    padding: 5px 5px 5px 15px;
                    border-radius: 6px;
                    font-weight: normal;
                    &:hover {
                        cursor: pointer;
                        font-weight: bold;
                        background-color: rgb(228, 224, 224);
                    }
                }
            }
        }
    }
}